import './App.css';

import { useState, useRef } from 'react';
import { Route, Routes, useNavigate } from 'react-router-dom';
//import Modal from 'react-modal';
import AgreementModal from './components/AgreementModal';

import Main from './components/Main';
import Info from './components/Info';
import MapShortCut from './components/MapShortCut';
import Support from './components/Support';


/* ---- Header ---- */
function Header(props) {
  const lis = [];
  for(let i = 0; i < props.topics.length; i++){
    let t = props.topics[i];
    lis.push(<li id={t.value} key={t.value} onClick={event=>{
      event.preventDefault();
      props.onChangeMode(event.target.id);
    }}>{t.label}</li>);
  }
  return <header>
    <ul>{lis}</ul>
  </header>
}

/* ---- Footer ---- */
//npm install react-modal
//https://velog.io/@seungsang00/React-React-Modal
function Footer() {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [modalIdx, setModalIdx] = useState(0);

  const modalContent = [
    {title : '', content : '               .'},
    {title : '', content : '  ( "" )       ,           .'},
    {title : '', content : '     .       .'},
  ];
  const childModalClose = e =>{
    setModalIsOpen(false);
  };
  return <footer className='footer-wrap'>
      <div className='f-top'>
        <p onClick={ e =>{ setModalIsOpen(true); setModalIdx(0); }}></p>
        <p onClick={ e =>{ setModalIsOpen(true); setModalIdx(1); }}></p>
        <p onClick={ e =>{ setModalIsOpen(true); setModalIdx(2); }}></p>
      </div>
      <div className='f-con'>
        <p>Address. Gangnam-gu, Seoul</p>
        <p>Tel. 02-0000-1111 (2222)</p><p>Fax. 02-0000-0000</p>
        <p>COPYRIGHT (C) 2023 AHNCHEER ALL RIGHTS RESERVED.</p>
      </div>

      <AgreementModal 
          isOpen={modalIsOpen} 
          data={modalContent[modalIdx]}  
          closeModal={childModalClose}></AgreementModal>
  </footer>
}


/* ==== App ==== */
function App() {
  const gnbList = [
    {label : 'Main', value : '/'}, 
    {label : 'Info', value : '/info'}, 
    {label : 'MapShortcut', value : '/map'},
    {label : 'Support', value : '/support'}
  ];
  const movePage = useNavigate();

  return (
    <div className="AppWrapper">
      <Header topics={gnbList} onChangeMode={(value)=>{
        movePage(value);
      }}></Header>

      <div className='MainContent'>
        <Routes>
          <Route path='/' element={<Main />} />
          <Route path='/info' element={<Info />} />
          <Route path='/map' element={<MapShortCut />} />
          <Route path='/support' element={<Support />} />
        </Routes>
      </div>

      <Footer></Footer>
    </div>
  );
}

export default App;
